.App {
  text-align: center;
  background-image: linear-gradient(grey .15em, transparent .15em), linear-gradient(90deg, grey .15em, transparent .15em);
  background-size: 8em 8em;  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'Flexo-Medium';
  src: local('Flexo-Medium'), url(./fonts/Flexo-Medium.woff) format('woff');
}